
@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,300;0,500;0,700;1,300;1,400&family=Play&display=swap');

*,
*::after,
*::before{

  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html{
  scroll-behavior: smooth;
  font-size: 62.5%;
  overflow-x: hidden;
}
body{
  overflow-x: hidden;
}
a,a:hover{
  text-decoration: none;
}
a:hover{
  color: var(--primary-color);
}
ul,ol{
  margin: 0;
  padding: 0;
}
:active,:hover,:focus{
  outline: 0 !important;
  outline-offset: 0;
}
:root{
  --primary-color:#5bc1ac;
  --secondary-color: #5a6f80;
  --section-bg-color: #d8e8f7;
  --site-footer-bg-color: #44525d;
  --custom-btn-bg-color: #597081;
  --custom-btn-bg-hover-color: #5bc1ac;
  --dark-color: #000000;
  --p-color: #717275;
  --border-color: #e9eaeb;
  --body-font-family: 'Mulish', sans-serif;
}
.main-btn{
  display: inline-block;
  height: 4.7rem;
  width: 12rem;
  border-radius: 2rem;
  border:0.1875rem solid var(--secondary-color);
  background: transparent;
  color: var(--secondary-color);
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: capitalize;
  -webkit-transition: all .4s ease-out 0s;
  -o-transition: all .4s ease-out 0s;
  -moz-transition: all .4s ease-out 0s;
  transition: all .4s ease-out 0s;
}
.main-btn:hover{
  background-color: var(--primary-color);
  color: #fff;
}
::-webkit-scrollbar{
  width: 0.625rem;
}
::-webkit-scrollbar-track{
  background: var(--section-bg-color);
}
::-webkit-scrollbar-thumb{
  background: var(--secondary-color);
}

h1{
  font-size: 3rem;
  font-weight: 900;
  margin-bottom: 2rem;
  color: var(--primary-color);
}
h2{
  font-size: 2.6rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  color: var(--secondary-color);
}
h3{
  font-size: 2rem;
  font-weight: 600;
  color: var(--secondary-color);
}
h4{
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--primary-color);
}
h5{
  font-size: 3.3rem;
  font-weight: 700;
  color: #ffffff;
}

/* Banner Section */
.banner-container{
  width: 100%;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 1.5rem;
  padding: 0.8rem 1rem;
}

.banner-container .left-side-banner{
  display: grid;
  padding-left: 4.5rem;
  
}
.banner-container .left-side-banner .banner-inside-div{
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  padding: 0 7.5rem;
}
.banner-container .left-side-banner .banner-inside-right-div{
  margin-left: auto;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  padding-left: 5rem;
}
.banner-container .banner-inside-div li{
  list-style: none;
}
.banner-container .banner-inside-div i{
  margin-right: 0.8rem;
}
.banner-inside-right-div li{
  list-style: none;
}
.banner-inside-right-div i{
  margin-right:0.6rem;
}

/* Navbar */
.navbar-conatiner{
  height: 15vh;
  display: flex;
  justify-content: center;
  text-align: center;
}

.navbar-conatiner ul li{
  font-size: 1.5rem;
  padding: 0 2.5rem;
  color: var(--secondary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-conatiner li a:hover{
  color: var(--primary-color)!important;
}

.navbar-conatiner .navbar-brand{
  display: flex;
}
.navbar-brand img{
  height: 8rem;
  width: 10rem;
}
.navbar-conatiner .navbar-brand span{
  margin-top: 1.5rem;
  color:var(--primary-color);
  font-size: 1.5rem;
  font-weight: 700;
}
.navbar-conatiner .navbar-brand span small{
  color:var(--secondary-color);
}
.navbar-toggler:focus{
  overflow: unset;
  border: unset;
  box-shadow: none;
}

.Hero-Section{
  width:100vw;
}
.Hero-Container{
  padding: 2rem;
  /* background-color: var(--section-bg-color); */
  width: 100vw;
}

/* Hero Section */
.Hero-Container-Carousel img{
  height: 40rem;
  width: 100rem;
  clip-path: polygon(0 0, 100% 0, 85% 39%, 62% 100%, 0 100%, 0% 50%);
}

.Hero-Container-Carousel h2{
  font-size: 3.8rem;
}
.Hero-Container-Carousel .carousel-caption{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.Hero-Container-Carousel .carousel-caption{
  width: 30%;
  margin-left: 65rem;
  margin-bottom: 5rem;
}
.Hero-Container-Carousel .carousel-caption p{
  color: var(--p-color);
  font-size: 1.4rem;
}

/* HeroTwo Container */
.HeroTwo-Container{

 width: 100%;
  margin-top: 2rem;
  margin-bottom: 4rem;
  padding: 3rem;
}

.HeroTwo-Container h2{
  font-size: 3.5rem;
  font-weight: 700;
  padding-bottom: 4rem;
}

.HeroTwo-Container .Hero-Container-Cards{
  padding: 2 2.5rem;
}

.HeroTwo-Container .Hero-Container-Cards:hover{
  /* background-color: rgb(162, 70, 70); */
  height:20rem;
  padding: 1.5rem;
  background-color: #ffffff;
  border-radius: 3rem;
  box-shadow: 1rem 0.4rem 3rem;
  cursor: pointer;
  transition: all 0.5s ease-in;
}

.AboutUs-Container{
  margin-top: 2rem;
  background-color: var(--section-bg-color);
  padding: 1.3rem;
}
.AboutUs-Container .leftSide-AboutUs img{
