  padding: 1.2rem;
}
.AboutUs-Two-Container .AboutUs-Two-RightSide i:hover{
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
}
.AboutUs-Two-Mid{
  height: 35vh;
  position: relative;
  overflow: hidden;
  background-color: var(--section-bg-color);
  padding:6rem;
}
.AboutUs-TwoMidRight {
  
  font-size: 1.6rem;
  
}
.Secondary-btn{
  background: var(--custom-btn-bg-color);
  color: white;
  border: transparent;
  border-radius: 3rem;
  height: 5rem;
  width: 20rem;
  font-size: 1.6rem;
  -webkit-transition: all .4s ease-out 0s;
  -o-transition: all .4s ease-out 0s;
  -moz-transition: all .4s ease-out 0s;
  transition: all .4s ease-out 0s;
}
.Secondary-btn:hover{
