}
.OurCauses-Cards .card-img{
  border-radius: 2.6rem;
}
.OurCauses-Cards h1{
  font-size: 2.5rem;
  font-weight: 600;
}
.OurCauses p{
  font-size: 1.4rem;
  color: var(--p-color);
}
.OurCauses-Cards:hover{
  color: #646363;
  height: 45rem;
  background-color: #ffffff;
  box-shadow: 0.6rem 0.6rem 0.6rem;
